import React, { Component } from 'react';
import logo from '../logo.svg';
import Footer from './Footer';
import ToggleCredits from './ToggleCredits';

class Release extends Component {
    constructor(props) {
        super(props);
        this.state = {
            creditText: 'Hide Credits',
            creditsState: 'collapse',
            result: this.props,
            tracklist: [],
            extraartists: [],
            extraartistsTrack: [],
            toggleCreditText: 'Show Credits',
            footer: 'secondary'
        }
        // console.log(this.props);
    }

    handleModal = (e) => {
        e.preventDefault();
        console.log('Modal Opened');
    }

    handleClick = (e) => {
        e.preventDefault();

        this.setState({ toggleCreditText: 'Hide Credits' });  

        const elem = document.querySelectorAll('.tracklist-item');
        [].forEach.call(elem, (el) => {
            if(el.classList.contains('collapse')){                
                el.classList.remove('collapse');
                sessionStorage.removeItem('creditsState');
                this.setState({creditText: 'Show Credits'}, () => {
                    sessionStorage.removeItem('creditText');
                    // sessionStorage.setItem('creditText', JSON.stringify(this.state.creditText));
                });
            } else {
                el.classList.add("collapse");
                this.setState({ creditsState: 'collapse', creditText: 'Hide Credits' }, () => {
                    sessionStorage.setItem('creditText', JSON.stringify(this.state.creditText));
                    sessionStorage.setItem('creditsState', JSON.stringify(this.state.creditsState));
                });                   
            }
        });   
    }

    componentDidMount = () => {
        const {id} = this.props.match.params;
        fetch(`https://api.discogs.com/releases/${id}`)
        .then(res => res.json())
        .then(data => {
            console.log(data);
                this.setState({
                    tracklist: data.tracklist,
                    extraartists: data.extraartists,
                    extraartistsTrack: data.tracklist[0].extraartists
                }, () => localStorage.setItem('tracklist', JSON.stringify(data.tracklist)));
        });
    }

    render(){
        let releaseContent;
        const { id } = this.props.match.params;
        const { result } = this.props.location.state;
        const { tracklist, extraartists, extraartistsTrack } = this.state;   
        
        if(tracklist === undefined || tracklist.length === 0) {
            return(
                <div className="loading-container">
                    Loading...
                </div>
            )
        }

        releaseContent = (
             <React.Fragment>
                <div className="logo logo-search release">
                    <img src={logo} alt="logo" />
                </div>                                
                <div className="container release">
                <div className="release">
                    <div className="release-top">
                        <div className="release-top-left">
                            <div className='release-image'>
                                <a href={`/artist/${id}`} onClick={this.handleModal}>
                                    <img src={result.cover_image} alt={result.title} />
                                </a>
                                <div className="release-image-link">
                                    <a href={`/artist/${id}`} onClick={this.handleModal}>More Images</a>
                                </div>
                            </div>  
                        </div>
                        <div className="release-top-right">
                            <div className="release-title"><p>{result.title} </p></div>
                            <div className="release-label"><p>Label: {result.label[0]}</p></div>
                            <div className="release-format"><p>Format: {result.format[0]+', '+result.format[1]}</p></div>
                            <div className="release-country"><p>Country: {result.country}</p></div>
                            <div className="release-year"><p>Released: {result.year}</p></div>
                            <div className="release-genre"><p>Genre: {result.genre}</p></div>
                            <div className="release-style"><p>Style: {result.style[0] && result.style[1]  ? result.style[0]+', '+result.style[1] : ''}</p></div>
                        </div>
                        <div className="release-collection-status"></div>
                    </div>
                    <div className="release-bottom">
                        <div className="tracklist">
                             <div className="tracklist-top">
                                <p>Tracklist</p>     
                                <ToggleCredits
                                    toggleCreditText={this.state.toggleCreditText}
                                    handleClick={this.handleClick}
                                    extraartistsTrack={extraartistsTrack}
                                />
                             </div>
                             {tracklist.map((track, index) => (
                                <div key={index} className={`tracklist-item`+(sessionStorage.getItem("creditsState") ? ' collapse' : ' not true' )}>
                                    <p>{track.position ? track.position : ''} {track.title ? track.title : ''} <span className="track-duration">{track.duration !== '' ? track.duration : ''}</span></p>
                                    {track.extraartists ? track.extraartists.map((extraartist, index) => (
                                    <p key={index} className="extraartists">                                       
                                        {extraartist.role ? extraartist.role + ' - ' + extraartist.name : ''}
                                        {/* {extraartist.role} - {extraartist.name} */}
                                    </p>
                                    )) : ''}
                                </div>
                             ))}
                        </div>
                        <div className="credits">
                             <div className="credits-top">
                                <p>Credits</p>
                             </div>
                             {extraartists.map((artist, index) => (
                                <div key={index} className="credits-item">
                                    <p>{artist.role} - {artist.name} (tracks: {artist.tracks})</p>
                                    {/* {track.extraartists.map((extraartist, index) => (
                                    <p key={index} className="extraartists">
                                        {extraartist.role}
                                    </p>
                                    ))} */}
                                </div>
                             ))}
                        </div>
                    </div>
                </div>
                </div>
                <Footer footer={this.state.footer} />
             </React.Fragment>
        );
        return(
            <React.Fragment>
                {releaseContent}
            </React.Fragment>
        )
    }
}
export default Release;